import { useState, useCallback } from "react"
import { Box, Typography, TextField, Button, CircularProgress } from "@mui/material"
import { useTheme } from "@mui/material/styles"
import Header from "../../components/Header.jsx"
import Footer from "../../components/Footer.jsx"
import AICodeOutput from "./components/AICodeOutput.jsx"
import { useActionState } from "react"
import {generateCode} from "../../actions/ai-coding"; // Import useActionState

export default function AICodingPage() {
    const theme = useTheme()
    const [prompt, setPrompt] = useState("")
    //  'python'
    const [language, setLanguage] = useState("python") // Default language for AI output
    const [state, formAction, isPending] = useActionState(generateCode, { success: false, code: "", error: "" })

    const handlePromptChange = useCallback((e) => {
        setPrompt(e.target.value)
    }, [])

    return (
        <Box
            sx={{
                display: "flex",
                flexDirection: "column",
                minHeight: "100vh",
                backgroundColor: theme.palette.background.default,
            }}
        >
            <Header />
            <Box
                sx={{
                    flexGrow: 1,
                    padding: { xs: 2, sm: 3, md: 4 },
                    boxSizing: "border-box",
                    display: "flex",
                    flexDirection: { xs: "column", md: "row" },
                    gap: { xs: 3, md: 4 },
                }}
            >
                {/* Left Panel: Prompt Input */}
                <Box
                    sx={{
                        flex: 1,
                        display: "flex",
                        flexDirection: "column",
                        gap: 3,
                        minWidth: { md: "40%" }, // Ensure it doesn't shrink too much
                    }}
                >
                    <Typography variant="h4" component="h1" sx={{ color: theme.palette.text.primary, fontWeight: 700 }}>
                        AI Code Generator
                    </Typography>
                    <Typography variant="body1" sx={{ color: theme.palette.text.secondary, marginBottom: 2 }}>
                        Describe the code you need, and our AI will generate it for you. Default language is Python.
                    </Typography>
                    <TextField
                        label="Code Description"
                        multiline
                        rows={10}
                        fullWidth
                        variant="outlined"
                        value={prompt}
                        onChange={handlePromptChange}
                        placeholder="e.g., 'A Python function to reverse a string' or 'JavaScript script to read a CSV file'"
                        sx={{
                            "& .MuiOutlinedInput-root": {
                                backgroundColor: theme.palette.background.paper,
                                "& fieldset": { borderColor: theme.palette.divider },
                                "&:hover fieldset": { borderColor: theme.palette.primary.main },
                                "&.Mui-focused fieldset": { borderColor: theme.palette.primary.main },
                            },
                            "& .MuiInputBase-input": { color: theme.palette.text.primary },
                            "& .MuiInputLabel-root": { color: theme.palette.text.secondary },
                            "& .MuiInputLabel-root.Mui-focused": { color: theme.palette.primary.main },
                        }}
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={() => formAction(prompt)} // Pass prompt directly to the action
                        disabled={isPending || !prompt.trim()}
                        sx={{ padding: "12px 24px", fontSize: "1rem", fontWeight: 600 }}
                    >
                        {isPending ? <CircularProgress size={24} color="inherit" /> : "Generate Code"}
                    </Button>
                    {state.error && (
                        <Typography color="error" sx={{ marginTop: 2 }}>
                            {state.error}
                        </Typography>
                    )}
                </Box>

                {/* Right Panel: Generated Code Output */}
                <Box sx={{ flex: 1, minHeight: { xs: "400px", md: "auto" } }}>
                    {/*  language prop  state  language */}
                    <AICodeOutput code={state.code} language={language} />
                </Box>
            </Box>
            <Footer />
        </Box>
    )
}
