import { generateText } from "ai"
import { groq } from "@ai-sdk/groq"

export async function generateCode(prompt) {
    try {
        const { text } = await generateText({
            //  apiKey
            model: groq("llama3-8b-8192", { apiKey: process.env.GROQ_API_KEY }),
            prompt: `Generate code based on the following description. Provide only the code, no explanations or extra text. If the language is not specified, default to Python.
      Description: ${prompt}`,
            temperature: 0.7,
        })
        return { success: true, code: text }
    } catch (error) {
        console.error("Error generating code:", error)
        return { success: false, error: "Failed to generate code. Please try again." }
    }
}
